.header {
    background-color: #333;
    color: #fff;
    padding: 1rem 0;
  }
  
  .header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
  }
  
  .header .logo {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .header .nav {
    display: flex;
    gap: 1rem;
  }
  
  .header .nav a {
    color: #fff;
    text-decoration: none;
    font-size: 1rem;
  }
  
  .header .nav a:hover {
    text-decoration: underline;
  }